.NavBar-display {
  align-content: center;
  background-color: #6c89b3;
  border: 1px solid black;
  display: flex;
  flex-flow: row wrap;
  height: 30px;
  justify-content: space-around;
  left: 1px;
  list-style-type: none;
  margin: auto;
  padding: 0 30px;
  position: fixed;
  right: 1px;
  top: 0;
}

.NavBar-link {
  color: white;
  padding: 0 20px;
  text-decoration: none;
}

.NavBar-link:hover {
  color: black;
}
