.About-body {
  font-weight: 500;
  margin: auto;
  text-align: justify;
  text-indent: 2em;
  width: 50%;
}

.About-body-img-container {
  align-content: center;
  display: flex;
}

.About-body-img {
  border: 5px inset rgb(114, 111, 111);
  box-shadow: 5px 5px 5px black;
  margin: auto;
  width: 40%;
}

@media only screen and ( max-width : 769px ) {

  .About-body-img {
    display: none;
  }
}
